import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  changeCountry,
  changeEndMonth,
  changeStartMonth,
  changeMax,
  changeMin,
  changeDisplay,
  changeJudgeVis,
} from "../stores/details";
import WorldMap from "./WorldMap";
import CountryHeatMap from "./CountryHeatMap";
import Bargraph from "./Bargraph";

const JudgeVis = () => {
  const dispatch = useDispatch();
  const startMonth = useSelector((state) => state.detail.startMonth);
  const endMonth = useSelector((state) => state.detail.endMonth);
  const judgeVis = useSelector((state) => state.detail.judgeVis);
  const isRegionShowed = useSelector((state) => state.detail.isRegionShowed); //
  //console.log("judgeVis :" + judgeVis);

  if (
    (judgeVis === 0 && !isRegionShowed) ||
    (judgeVis === 0 && isRegionShowed)
  ) {
    //
    return (
      <div className="card-content">
        <div className="content">
          <p style={{ fontSize: "1.25rem" }}>
            
          </p>
        </div>
      </div>
    );
  } else if (judgeVis === 1) {
    //
    return (
      <div className="card-content p-1" style={{ height: "100%" }}>
        <WorldMap />
      </div>
    );
  } else if (judgeVis === 2) {
    //
    return (
      <CountryHeatMap />
    );
  } else {
    //
    return (
      <div className="card-content p-1" style={{ height: "100%" }}>
        <Bargraph />
      </div>
    );
  }
};

export default JudgeVis;
